@import url(https://fonts.googleapis.com/css2?family=Texturina:wght@100;900&display=swap);
* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Texturina', serif;
  background-color: #70D6FF;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 200px;
    justify-content: center;
    position: absolute;
    top:0;
}

.loading__title {
    font-size: 30px;
    margin-top: 50px;
}

.loading__big-icon {
    font-size: 200px;
    animation: rotations 3s infinite;
    animation-timing-function: linear;
}

@keyframes rotations {
    0% {
      transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
  }
.card {
    width: 100%;
    padding: 10px;
    background-color: lightgrey;
    cursor: pointer;
}

.card__img-wrapper {
    position: relative;
    padding-top: 100%;
    width: 100%;
    overflow: hidden;
}

.img {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
}

.card__title {
    text-align: center;
}

.card__icon {

}

.card__author {

}

.card__text {

}

.card__title::after {
    content: '*'
}
.scroll-top {
    width: 50px;
    height: 50px;
    border: 3px solid #FF9770;
    padding: 10px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: opacity 0.2s;
    position: fixed;
    right: 30px;
    bottom: 30px;
    background-color: transparent;
  }
  
  .scroll-top:hover {
    opacity: 0.5;
  }
  
  .scroll-top__icon {
    font-size: 30px;
    color: #FF9770;
  }
.gal__img-wrapper {
    display: flex;
    width: 80%;
    flex-direction: column;
    align-items: center;
    position: relative;
    padding-top: 60%;
    overflow: hidden;
    position: relative;
}

.gal-img {
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    z-index: 2;
}

.gal__btns {
    width: 100%;
    display:flex;
    justify-content: space-around;
    margin-top: 20px;
}

.intro {
  font-size: 150px;
  font-weight: bold;
  position: absolute;
  z-index: 2;
  animation: stupid 7s linear 1;
}

@keyframes stupid {
  0% {
    transform: rotate(0) translateX(0) translateY(50px) scale(0);
    color: red;
    opacity: 0.8;
  }
  20% {
    transform: rotate(-45deg) translateX(300px) translateY(-100px) scale(0.5);
    color: green;
    opacity: 0.8;
  }
  40% {
    transform: rotate(45deg) translateX(100px) translateY(50px) scale(0.8);
    color: blue;
    opacity: 0.8;
  }
  50% {
    transform: rotate(0) translateX(50px) translateY(75px) scale(0.9);
    color: rgb(39, 7, 68);
    opacity: 0.8;
  }
  60% {
    transform: rotate(-360deg) translateX(0) translateY(100px) scale(1);
    color: rgb(194, 201, 129);
    opacity: 1;
  }
  75% {
    opacity: 0;
    transform: scale(10);
  }
}

.nav {
  display: flex;
  justify-content: space-around;
  width: 100%;
}

.nav__item {
  font-size: 30px;
  font-weight: bold;
  position: relative;
}

.nav__search-icon {
  cursor: pointer;
  top: 5px;
  opacity: 0.5;
  position: absolute;
  right: 5px;
  transition: opacity 0.2s;
}

.nav__search-icon:hover {
  opacity: 1;
}

.margin-bottom--10 {
    margin-bottom: 10px;
  }
  
  .margin-bottom--30 {
    margin-bottom: 30px;
  }
  
  .flex {
    display: flex;
  }
  
  .space--between {
    justify-content: space-between;
  }
  
  .center-xs {
    justify-content: center;
  }

  .w100 {
      width: 100%;
  }
